@import './mixins.scss';
@import './reset.scss';


/*--------------------------------------------*/
/* common */
/*--------------------------------------------*/

html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
*, *:before, *:after {
  box-sizing: inherit;
}

a {
	color: inherit;
}

body{
	margin: 0;
	padding: 0;
	background-color: #EFEFEF;
}

canvas{
	display: block;
	top:0;
	left:0;
}

#banner{
	/* bigbox */
	width: 300px;
	height: 250px;
	overflow: hidden;
	position: relative;
	background-color: #00a950;
	-webkit-text-size-adjust: 100%;
	opacity: 1;
	color: white;
	font-family: ff;
}

canvas, .text, .frame, .outline, img{
	position: absolute;
}

img{
	vertical-align:top;
	 -webkit-backface-visibility: hidden;
    -webkit-transform: translateZ(0) scale(1.0, 1.0);
    
    image-rendering: optimizeQuality;
	image-rendering: optimizeSpeed;
	image-rendering: -moz-crisp-edges;

}

.outline{
	z-index: 99;
	width: inherit;
	height: inherit;
	border: 1px solid black;
	pointer-events: none;
	position: absolute;
}

.frame{
	width: inherit;
	height: inherit;
	opacity: 0;
	top:0;
	left:0;
}

p{
	display: inline-block;
	// font-weight: normal !important;
  	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
}

#t1 span{
	display: block;
}

sup{
	font-size: 50%;
	vertical-align: top;
	position: relative;
	left:-3px;
}



.shadowed{
	-webkit-filter: drop-shadow(0px 0px 9px rgba(0,0,0,.4));
	filter: drop-shadow(px 0px 9px rgba(0,0,0,.4));
}
.bg{
	width: 100%;
	height: 100%;
}


