@font-face {
    font-family: 'ff';
    src: url(data:application/font-woff;charset=utf-8;base64,) format('woff');
    font-weight: normal;
    font-style: normal;
}



#legal-copy {
	font-size: 9px;
	background-color: rgba(0,0,0,.9);
	padding: 15px 11px;
	@include pos(0, 0);
	line-height: 13px;
	opacity:0;
	color: white;
	width: 100%;
	right: 0px;
	font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;

}

#legal-button{
	font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;	
	font-size: 9px;
	@include pos(11, 210);
	color: white;
	text-decoration: underline;
	cursor: pointer;
}

.bg{
	opacity:0.65;
}



#t1, #t2, #t3, #t4, #t5, .t1, .t2, #t1 span{	
	background: -webkit-linear-gradient(#FFFFFF, #C0C0C0);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}


#phoneMain{
	#screen{
		// background-color: #00a950;
		/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#00a950+0,001c11+100 */
